"use client";

import { useState } from "react";
import { Edit3, Trash2, Check, X, Plus, Search } from "lucide-react";

/* ======================
   Currency Formatter
====================== */
const currency = (val = 0) =>
  new Intl.NumberFormat("en-PH", {
    style: "currency",
    currency: "PHP",
    minimumFractionDigits: 2,
  }).format(val);

export default function TransactionTable({
  transactions = [],
  loading,
  onAddTransaction,
  onEditTransaction,
  onDeleteTransaction,
}) {
  /* ======================
     STATE
  ====================== */
  const [editingId, setEditingId] = useState(null);
  const [rowForm, setRowForm] = useState({});
  const [showForm, setShowForm] = useState(false);

  const [filters, setFilters] = useState({
    type: "all",        // all | income | expense
    category: "all",
    fromDate: "",
    toDate: "",
    search: "",
  });

  const [form, setForm] = useState({
    date: "",
    description: "",
    category: "",
    payee: "",
    deposit: "",
    withdrawal: "",
  });

  /* ======================
     FILTER LOGIC
  ====================== */
  const filteredTransactions = transactions.filter((t) => {
    // TYPE
    if (filters.type === "income" && t.deposit <= 0) return false;
    if (filters.type === "expense" && t.withdrawal <= 0) return false;

    // CATEGORY
    if (filters.category !== "all" && t.category !== filters.category)
      return false;

    // DATE RANGE
    if (filters.fromDate && new Date(t.date) < new Date(filters.fromDate))
      return false;
    if (filters.toDate && new Date(t.date) > new Date(filters.toDate))
      return false;

    // SEARCH (description or payee)
    if (
      filters.search &&
      !`${t.description ?? ""} ${t.payee ?? ""}`
        .toLowerCase()
        .includes(filters.search.toLowerCase())
    )
      return false;

    return true;
  });

  /* ======================
     HANDLERS
  ====================== */
  const startEdit = (t) => {
    setEditingId(t.id);
    setRowForm({
      date: t.date,
      description: t.description,
      category: t.category,
      payee: t.payee,
      deposit: t.deposit || "",
      withdrawal: t.withdrawal || "",
    });
  };

  const saveEdit = (id) => {
    onEditTransaction(
      {
        date: rowForm.date,
        description: rowForm.description,
        category: rowForm.category,
        payee: rowForm.payee,
        deposit: Number(rowForm.deposit || 0),
        withdrawal: Number(rowForm.withdrawal || 0),
      },
      id
    );
    setEditingId(null);
  };

  /* ======================
     UNIQUE CATEGORIES
  ====================== */
  const categories = [
    ...new Set(transactions.map((t) => t.category).filter(Boolean)),
  ];

  /* ======================
     RENDER
  ====================== */
  return (
    <div className="space-y-4">

      {/* ================= FILTER BAR ================= */}
      <div className="bg-slate-50 border rounded-lg p-4">
        <div className="grid grid-cols-1 md:grid-cols-6 gap-3">

          <select
            className="border rounded px-3 py-2 text-sm"
            value={filters.type}
            onChange={(e) =>
              setFilters({ ...filters, type: e.target.value, category: "all" })
            }
          >
            <option value="all">All Types</option>
            <option value="income">Income</option>
            <option value="expense">Expense</option>
          </select>

          <select
            className="border rounded px-3 py-2 text-sm"
            value={filters.category}
            onChange={(e) =>
              setFilters({ ...filters, category: e.target.value })
            }
          >
            <option value="all">All Categories</option>
            {categories.map((cat) => (
              <option key={cat} value={cat}>
                {cat}
              </option>
            ))}
          </select>

          <input
            type="date"
            className="border rounded px-3 py-2 text-sm"
            value={filters.fromDate}
            onChange={(e) =>
              setFilters({ ...filters, fromDate: e.target.value })
            }
          />

          <input
            type="date"
            className="border rounded px-3 py-2 text-sm"
            value={filters.toDate}
            onChange={(e) =>
              setFilters({ ...filters, toDate: e.target.value })
            }
          />

          <div className="relative col-span-2">
            <Search className="absolute left-3 top-2.5 w-4 h-4 text-slate-400" />
            <input
              type="text"
              placeholder="Search description or payee"
              className="border rounded pl-9 pr-3 py-2 text-sm w-full"
              value={filters.search}
              onChange={(e) =>
                setFilters({ ...filters, search: e.target.value })
              }
            />
          </div>
        </div>
      </div>

      {/* ================= TABLE ================= */}
      <div className="border rounded-lg overflow-hidden bg-white">
        <table className="w-full text-sm">
          <thead className="bg-slate-100 text-slate-700">
            <tr>
              <th className="px-4 py-3 text-left">Date</th>
              <th className="px-4 py-3 text-left">Description</th>
              <th className="px-4 py-3 text-left">Category</th>
              <th className="px-4 py-3 text-left">Payee</th>
              <th className="px-4 py-3 text-right">Deposit</th>
              <th className="px-4 py-3 text-right">Withdrawal</th>
              <th className="px-4 py-3 text-right">Balance</th>
              <th className="px-4 py-3 text-center">Actions</th>
            </tr>
          </thead>

          <tbody>
            {filteredTransactions.map((t) => (
              <tr key={t.id} className="border-t hover:bg-slate-50">

                {editingId === t.id ? (
                  <>
                    <td className="px-4 py-2">
                      <input
                        type="date"
                        className="border rounded px-2 py-1 w-full"
                        value={rowForm.date}
                        onChange={(e) =>
                          setRowForm({ ...rowForm, date: e.target.value })
                        }
                      />
                    </td>

                    <td className="px-4 py-2">
                      <input
                        className="border rounded px-2 py-1 w-full"
                        value={rowForm.description}
                        onChange={(e) =>
                          setRowForm({ ...rowForm, description: e.target.value })
                        }
                      />
                    </td>

                    <td className="px-4 py-2">
                      <input
                        className="border rounded px-2 py-1 w-full"
                        value={rowForm.category}
                        onChange={(e) =>
                          setRowForm({ ...rowForm, category: e.target.value })
                        }
                      />
                    </td>

                    <td className="px-4 py-2">
                      <input
                        className="border rounded px-2 py-1 w-full"
                        value={rowForm.payee}
                        onChange={(e) =>
                          setRowForm({ ...rowForm, payee: e.target.value })
                        }
                      />
                    </td>

                    <td className="px-4 py-2 text-right">
                      <input
                        type="number"
                        className="border rounded px-2 py-1 w-full text-right"
                        value={rowForm.deposit}
                        onChange={(e) =>
                          setRowForm({ ...rowForm, deposit: e.target.value })
                        }
                      />
                    </td>

                    <td className="px-4 py-2 text-right">
                      <input
                        type="number"
                        className="border rounded px-2 py-1 w-full text-right"
                        value={rowForm.withdrawal}
                        onChange={(e) =>
                          setRowForm({ ...rowForm, withdrawal: e.target.value })
                        }
                      />
                    </td>

                    <td className="px-4 py-2 text-right font-semibold">
                      {currency(t.balance)}
                    </td>

                    <td className="px-4 py-2 flex justify-center gap-2">
                      <button
                        onClick={() => saveEdit(t.id)}
                        className="text-emerald-600"
                      >
                        <Check size={16} />
                      </button>
                      <button
                        onClick={() => setEditingId(null)}
                        className="text-slate-500"
                      >
                        <X size={16} />
                      </button>
                    </td>
                  </>
                ) : (
                  <>
                    <td className="px-4 py-2">{t.date}</td>
                    <td className="px-4 py-2">{t.description}</td>
                    <td className="px-4 py-2">{t.category}</td>
                    <td className="px-4 py-2">{t.payee || "‚Äî"}</td>
                    <td className="px-4 py-2 text-right text-emerald-700">
                      {t.deposit ? currency(t.deposit) : "‚Äî"}
                    </td>
                    <td className="px-4 py-2 text-right text-rose-700">
                      {t.withdrawal ? currency(t.withdrawal) : "‚Äî"}
                    </td>
                    <td className="px-4 py-2 text-right font-semibold">
                      {currency(t.balance)}
                    </td>
                    <td className="px-4 py-2 flex justify-center gap-2">
                      <button
                        onClick={() => startEdit(t)}
                        className="text-slate-600"
                      >
                        <Edit3 size={16} />
                      </button>
                      <button
                        onClick={() => onDeleteTransaction(t.id)}
                        className="text-rose-600"
                      >
                        <Trash2 size={16} />
                      </button>
                    </td>
                  </>
                )}
              </tr>
            ))}

            {!filteredTransactions.length && (
              <tr>
                <td colSpan="8" className="px-6 py-8 text-center text-slate-500">
                  No transactions found
                </td>
              </tr>
            )}
          </tbody>
        </table>

        {loading && (
          <div className="p-4 text-center text-slate-500">
            Loading transactions‚Ä¶
          </div>
        )}
      </div>
    </div>
  );
}
