"use client";

import { useEffect, useState } from "react";
import { supabase } from "../../../../lib/supabaseClient";

export function useCashflowData() {
  const [transactions, setTransactions] = useState([]);
  const [chartData, setChartData] = useState([]);
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    fetchCashflow();
  }, []);

  const fetchCashflow = async () => {
    setLoading(true);

    // 1Ô∏è‚É£ Fetch transactions from Supabase
    const { data, error } = await supabase
      .from("transactions")
      .select("*")
      .order("date", { ascending: true});

    if (error) {
      console.error("‚ùå Supabase fetch error:", error.message);
      setLoading(false);
      return;
    }

    // 2Ô∏è‚É£ Clean deposit and withdrawal values
    const mapped = (data || [])
      .filter((row) => row.date)
      .map((row) => ({
        ...row,
        deposit: Number(String(row.deposit || "0").replace(/[‚Ç±, ]/g, "")),
        withdrawal: Number(String(row.withdrawal || "0").replace(/[‚Ç±, ]/g, "")),
        balance: Number(String(row.balance || "0").replace(/[‚Ç±, ]/g, "")), // use Supabase balance
      }));

    // 3Ô∏è‚É£ Save raw transactions for table
    setTransactions(mapped);

    // 4Ô∏è‚É£ Aggregate chart data per month
    const grouped = {};

    mapped.forEach((t) => {
      const d = new Date(t.date);
      const monthKey = `${d.toLocaleString("default", { month: "short" })} ${d.getFullYear()}`;

      if (!grouped[monthKey])
        grouped[monthKey] = { month: monthKey, income: 0, expense: 0, balance: 0 };

      grouped[monthKey].income += t.deposit || 0;
      grouped[monthKey].expense += t.withdrawal || 0;

      // Take the last transaction's Supabase balance in the month
      grouped[monthKey].balance = t.balance || grouped[monthKey].balance;
    });

    const monthlyArray = Object.values(grouped)
      .sort((a, b) => new Date(`${a.month}`) - new Date(`${b.month}`))
      .map((g) => ({ ...g, net: g.income - g.expense }));

    setChartData(monthlyArray);
    setLoading(false);
  };

  return { transactions, chartData, loading,  refetch: fetchCashflow};
}
